* {
  box-sizing: border-box; }

body {
  color: #222;
  background-color: #fff;
  font-size: 1rem; }
  @media only screen and (min-width: 768px) {
    body {
      font-size: 1.125rem; } }

.heading, .new-heading {
  color: #b67c00; }
  .content.content-dark .article-main .heading, .content.content-dark .article-main .new-heading {
    color: #e99f00; }

h1 {
  font-size: 2rem; }
  @media screen and (min-width: 640px) {
    h1 {
      font-size: 3rem; } }
  @media screen and (min-width: 640px) and (orientation: landscape) {
    h1 {
      font-size: 5rem; } }

.alert {
  padding: 0.5rem 2rem; }

.alert-ok {
  background: url("../assets/ico-ok.svg") no-repeat left center;
  color: green; }

.alert-error {
  background: url("../assets/ico-error.svg") no-repeat left center;
  color: red; }

.heading, .new-heading {
  color: #895d3f; }

.new-heading {
  background-color: yellow; }
